import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2019-05-15-anglesharp-v0121.md";
    const html = `
<p>Yesterday a small hotfix release for AngleSharp v0.12 was released. With AngleSharp v0.12.1 also all the major libraries have been updated to v0.12.1 as well:</p>
<ul>
<li>AngleSharp.Xml</li>
<li>AngleSharp.Io</li>
<li>AngleSharp.Css</li>
<li>AngleSharp.Js</li>
</ul>
<p>The hotfix corrected the assembly version of AngleSharp, which was still delivered at 0.11. An update to the build script should prevent such issues in the future.</p>
<p>We used the hotfix to also deliver some smaller enhancements, such as additions to the <code>Accessors</code> flags or an improvement to the <code>TaskEventLoop</code>.</p>
<p>In AngleSharp.Css we could add some missing attributes. In AngleSharp.Xml we fixed the namespace for the <code>WithXml</code> from <code>AngleSharp.Xml</code> to <code>AngleSharp</code>.</p>
<p>We also updated most of the sample repositories. In the next release we will revamp the documentation.</p>
`;
